.music-tree-wrap {
  justify-content: center;
  display: flex;
}

.music-tree {
  transform-origin: 0 0;
  transform: scale(var(--scale-factor, 1)) skew(0deg, -4.7deg);
  -webkit-user-select: none;
  user-select: none;
}

.music-tree .music-note {
  color: #fff;
  cursor: pointer;
  background-color: #f0f;
  border: none;
  border-radius: 50%;
  font-size: 1.125rem;
  font-weight: 600;
  transition: opacity .2s;
  position: absolute;
  transform: translate(-50%, -50%)skew(0deg, 4.7deg);
}

.music-tree .music-note[data-ratio="1"] {
  font-size: 2.25rem;
}

.music-tree .music-note[data-ratio="1"], .music-tree .music-note[data-ratio="0.5"], .music-tree .music-note[data-ratio="2"] {
  color: #f0f;
  background-color: #fff;
  border: 3px solid #f0f;
}

.music-tree .music-note:hover {
  opacity: .66;
}

.music-tree .music-chord {
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  cursor: pointer;
  z-index: -1;
  opacity: .33;
  background-color: #ff0;
  border: none;
  width: 221px;
  height: 190.52px;
  transition: opacity .2s;
  position: absolute;
  transform: translate(-28.1%, -120%)rotate(-30deg);
}

.music-tree .music-chord[data-quality="minor"] {
  background-color: #0ff;
  transform: translate(-71.5%, -119.9%)rotate(30deg);
}

.music-tree .music-chord:hover {
  opacity: .15;
}

.root-frequency-setter {
  position: absolute;
  top: 0;
}

.metronome {
  text-align: center;
  background-color: #f4f4f4;
  border: 1px solid #ddd;
  border-radius: 8px;
  max-width: 300px;
  margin: 0 auto;
  padding: 20px;
}

.tap-button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
}

.tap-button:hover {
  background-color: #555;
}

.bpm-display {
  margin: 10px 0;
  font-size: 20px;
}

.shared-select {
  border: 1px solid #333;
  border-radius: 4px;
  margin: 10px 0;
  padding: 5px;
  font-size: 16px;
}

canvas {
  border: 1px solid #000;
  margin: 10px;
}
